module.exports = ".Die .cube .side {\n  border-radius: 6px;\n}\n\n.die {\n  display: grid;\n  width: 100%;\n  height: 100%;\n  grid-template-columns: repeat(3, 1fr);\n  grid-template-rows: repeat(3, 1fr);\n  grid-gap: 4px;\n}\n\n.dot {\n  background-color: #131313;\n  border-radius: 50%;\n  opacity: 0;\n}\n\n[value='1'],\n[value='3'],\n[value='5'] {\n  .dot:nth-child(5) {\n    opacity: 1;\n  }\n}\n\n[value='2'],\n[value='3'],\n[value='4'],\n[value='5'],\n[value='6'] {\n  .dot {\n    &:nth-child(9),\n    &:nth-child(1) {\n      opacity: 1;\n    }\n  }\n}\n\n[value='4'],\n[value='5'],\n[value='6'] {\n  .dot {\n    &:nth-child(3),\n    &:nth-child(7) {\n      opacity: 1;\n    }\n  }\n}\n\n[value='6'] {\n  .dot {\n    &:nth-child(4),\n    &:nth-child(6) {\n      opacity: 1;\n    }\n  }\n}\n"